import Vue from 'vue';
import Router from 'vue-router';
import AllProducts from '../components/AllProducts';
import AllHoodie from '../components/AllHoodie';
import AllSepatu from '../components/AllSepatu';
import AllKaos from '../components/AllKaos';
import Product from '../components/Product';
import CartCheckout from '../components/CartCheckout';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '',
      name: 'All Products',
      component: AllProducts,
    },
    {
      path: '/Hoodie',
      name: 'Hoodie',
      component: AllHoodie,
    },
    {
      path: '/sepatu',
      name: 'Sepatu',
      component: AllSepatu,
    },
    {
      path: '/kaos',
      name: 'Kaos',
      component: AllKaos,
    },
    {
      path: '/product-details',
      name: 'Product',
      component: Product,
    },
    {
      path: '/checkout',
      name: 'Checkout',
      component: CartCheckout,
    },
  ],
});



